* {
  box-sizing: border-box;
}

body {
  color: #101010;
  background-image: url("bezhevyi-fon-uzor-vintazh-background-pattern-flower-vintage.a473b259.webp");
  background-size: contain;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 60px 0 0;
  font-family: Georgia, serif;
  display: flex;
}

.wrapper {
  background-color: #fff;
  flex-direction: column;
  flex-grow: 1;
  width: 90%;
  max-width: 800px;
  margin: 100px auto 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 0 10px #0000001a;
}

aside {
  float: left;
  width: 20%;
  padding: 8px;
}

header {
  z-index: 1000;
  background-image: url("vintage.46735c85.jpg");
  background-size: contain;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  box-shadow: 0 2px 5px #0000001a;
}

h1 {
  text-align: center;
  margin-bottom: 10px;
  font-size: 6vw;
  font-weight: bold;
}

nav {
  justify-content: center;
  gap: 20px;
  margin: 20px 0;
  display: flex;
}

nav h2 {
  margin: 0;
  font-size: 2rem;
}

.scroll-link {
  color: #000;
  font-weight: normal;
  text-decoration: none;
}

.scroll-link:hover {
  text-decoration: underline;
}

.section {
  margin-bottom: 20px;
  padding-top: 60px;
  scroll-margin-top: 80px;
  position: relative;
}

.book {
  background-image: url("vintage.46735c85.jpg");
  background-size: cover;
  border: 1px solid #ccc;
  border-radius: 10px;
  margin-bottom: 20px;
  padding: 20px;
  transition: transform .2s;
  box-shadow: 0 4px 8px #0000001a;
}

.book:hover {
  transform: scale(1.02);
}

.book h2 {
  color: #000;
  margin: 0 0 10px;
  font-size: 1.8rem;
}

.book p {
  margin: 5px 0;
  line-height: 1.6;
}

.book img {
  border-radius: 5px;
  max-width: 100%;
  height: auto;
}

.book strong {
  color: #000;
}

.book ul {
  padding-left: 20px;
}

.book a {
  color: #fff;
  background-color: #3d3e3f;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px 15px;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.book a:hover {
  background-color: #3d3e3f;
}

.book hr {
  border: none;
  border-top: 1px solid #ccc;
  margin: 15px 0;
}

.footer {
  clear: both;
  text-align: center;
  color: #fff;
  background-color: #333;
  width: 100%;
  margin-top: auto;
  padding: 10px;
  display: block;
}

.footer img {
  margin-top: 10px;
}

@media (width >= 768px) {
  aside {
    float: left;
    width: 40%;
  }

  article {
    float: left;
    width: 60%;
  }

  h1 {
    font-size: 2.5rem;
  }

  nav h2 {
    font-size: 2rem;
  }

  .scroll-link {
    font-size: 1.4rem;
  }

  .section {
    margin-bottom: 40px;
  }

  nav h2 a {
    font-size: 2rem;
  }
}

@media (width <= 600px) {
  header {
    padding: 10px;
  }

  nav {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  aside, article {
    float: none;
    width: 100%;
  }

  .wrapper {
    padding: 15px;
  }

  .book {
    padding: 8px;
  }

  h1 {
    font-size: 2rem;
  }

  nav h2 {
    font-size: 4vw;
  }

  nav h2 a {
    font-size: 2rem;
  }
}

@media (width >= 320px) and (width <= 1033px) {
  nav h2 a, a .scroll-link {
    font-size: 2rem;
  }
}

@media (width >= 320px) and (width <= 600px) {
  .wrapper {
    width: 95%;
  }

  h1 {
    font-size: 1.8rem;
  }

  .scroll-link {
    font-size: 4vw;
  }

  nav h2 a {
    font-size: 2rem;
  }
}

@media (width >= 601px) and (width <= 768px) {
  .wrapper {
    width: 90%;
  }

  h1 {
    font-size: 2rem;
  }

  .scroll-link {
    font-size: 3vw;
  }

  nav h2 a {
    font-size: 2rem;
  }
}

@media (width <= 320px) {
  nav h2 a {
    font-size: 5rem;
  }
}
